export default {
	name: 'select',
	props: {
		// placeholder: {
		// 	type: String,
		// 	default: '请输入信息'
		// },
		infoList: {
			type: Array,
			default () {
				return []
			}
		},
		value: {
			type: String,
			default: ''
		},
		showValue: {
			type: String,
			default: 'name'
		},
		emptyTips: {
			type: String,
			default: '暂无数据'
		},
		loading: {
			type: Boolean,
			default: false
		},
		btnStyleColor: {
			type: Object,
			default() {
				return {}
			}
		},
		uniShadow: {
			type: Boolean,
			default: true
		},
		type: {
			type: String,
			default: 'primary'
		},
		size: {
			type: String,
			default: 'medium'
		},
		isShowSelect: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			inputIsShow: false,
			inputVal: '',
			dataList: [],
			placeholder: '请输入',
			isShowClearIcon: false
		}
	},
	computed: {
		dataListLength() {
			return this.dataList.length
		}
	},
	watch: {
		value: {
			handler(newVal) {
				this.inputVal = newVal
			},
			immediate: true
		},
		infoList: {
			handler(newVal) {
				this.dataList = newVal
			},
			deep: true,
			immediate: true
		},
		inputVal(val) {
			if(!val.length) {
				this.isShowClearIcon = false
			}
		}
	},
	methods: {
		closeDielog() {
			this.inputIsShow = false
		},
		onFocus() {
			if(this.dataList.length) {
				this.inputIsShow = true
				this.isShowClearIcon = !!this.inputVal
			}
		},
		onSelectorClick(index) {
			this.inputIsShow = false
			this.isShowClearIcon = false
			this.inputVal = this.dataList[index][this.showValue]
			this.$emit('input', this.inputVal)
			this.$emit('change', this.dataList[index])
		},
		onInput() {
			this.$emit('input', this.inputVal)
		},
		handleSearch() {
			this.inputIsShow = true
			this.$emit('handleSearch')
		},
		clearInputValue() {
			this.inputVal = ''
		}
	}
}
